h1 {
  color: rgb(240, 199, 17);
  font-style: italic;
}

li {
  color: rgb(59, 9, 240);
  padding-top: 3px;
  font-size: 15px;
}

.note {
  color: rgb(236, 22, 219);
  padding-top: 5px;
  font-size: 15px;
}

.error {
  color: rgb(11, 83, 105);
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}